html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}

.clear {
  float: none;
  clear: both;
}

.container {
  width: 80%;
  margin: 0 auto;
  clear: both;
}

/*header */
header {
  width: 100%;
  background-image: url("/img/bg_header.png");
  background-size: 100% 100%;
  padding-top: 20px;
}

.header__menu {
  float: right;
}

.header__menu ul li {
  display: inline;
  margin-right: 51px;
}

.header__menu ul li:nth-child(6) {
  margin-right: 0px;
}

.header__menu ul li a {
  color: white;
  text-decoration: none;
  font-family: "Roboto" , sans-serif;
  font-size: 18px;
}

.header__logo {
  float: left;
  margin-left: 160px;
}

/*headerMore */
.header__more {
  width: 50%;
  float: left;
  margin-top: 217px;
  margin-left: 157px;
}

.header__more h3 {
  font-size: 36px;
  font-family: "Roboto" , sans-serif;
  color: white;
}

.header__more p {
  margin-top: 36px;
  margin-bottom: 80px;
  font-size: 20px;
  font-family: "Roboto" , sans-serif;
  color: white;
  line-height: 1.8;
  width: 580px;
}

.ghost__btn {
  text-transform: uppercase;
  display: inline-block;
  width: 250px;
  height: 70px;
  padding: 8px;
  color: #fff;
  border: 3px solid #fff;
  outline: none;
  text-decoration: none;
  background: rgba(255, 255, 255, 0);
  border-radius: 5px;
  font-family: "Lucida Grande", "Lucida Sans Unicode", Arial, sans-serif;
  letter-spacing: -1px;
  font-size: 20px;
  cursor: pointer;
}

.ghost__btn:hover {
  background: rgba(255, 255, 255, 0.3);
}

.header__image {
  margin-top: 110px;
  margin-bottom: 80px;
  width: 300px;
  float: right;
}

.header__image img {
  width: 100%;
  height: 50%;
}

/*How it triple */
.triple__block {
  width: 100%;
  background-color: white;
  padding-top: 100px;
  padding-bottom: 80px;
}

.h1 {
  text-align: center;
  font-size: 34px;
  font-family: "Roboto" , sans-serif;
  color: #343434;
}

.subtitle {
  text-align: center;
  color: #ff8400;
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.triple__info__block {
  width: 31%;
  float: left;
  margin-right: 3.5%;
  margin-top: 80px;
  text-align: center;
}

.triple__info__block:nth-child(3n+2) {
  margin-right: 0px;
}

.triple__info__block__img {
  display: block;
  width: 40%;
  margin: 0 auto;
}

.triple__info__block__p--title {
  color: #343434;
  font-weight: 600;
  margin-top: 50px;
  margin-bottom: 20px;
}

.triple__info__block__p--title a {
  color: #343434;
  text-decoration: none;
}

.triple__info__block__p--title a:hover {
  color: #5c5c5c;
}

.triple__info__block__p {
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
}

.triple__info__block__p--text {
  color: #5c5c5c;
}

/*Contact Block(email) */
.contacts__block {
  width: 100%;
  background-image: url("/img/bg_contact.png");
  background-size: 100% 100%;
  padding-top: 140px;
  padding-bottom: 152px;
  padding-left: 150px;
  color: white;
}

.contacts__block h3 {
  text-align: center;
  font-size: 30px;
  font-family: "Roboto" , sans-serif;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.contacts__block p {
  width: 750px;
  margin: 0 auto;
  margin-top: 10px;
  text-align: center;
  font-size: 18px;
  font-family: "Roboto" , sans-serif;
  color: white;
  line-height: 2;
}

.contacts__block input[name="email"] {
  color: white;
  width: 610px;
  border: 1px solid white;
  border-radius: 3px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  background: none;
  outline: none;
  margin-right: 10px;
}

.adress {
  padding-left: 200px;
  padding-top: 70px;
}

.textbox {
  display: inline-block;
  width: 600px;
  height: 60px;
  padding: 8px;
  color: #fff;
  border: 3px solid #fff;
  outline: none;
  text-decoration: none;
  background: rgba(255, 255, 255, 0);
  border-radius: 5px;
  margin-right: 20px;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
}

.btn__1 {
  text-transform: uppercase;
  display: inline-block;
  width: 200px;
  height: 60px;
  padding: 8px;
  color: #ff8400;
  border: 3px solid #fff;
  outline: none;
  text-decoration: none;
  background: #fff;
  border-radius: 5px;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
}

.btn__1:hover {
  background: #e9e9e9;
  border: 3px solid #e9e9e9;
}

/*footer */
footer {
  width: 100%;
  border-top: 2px solid #D8D8D8;
  background-color: white;
  padding-top: 10px;
  padding-bottom: 18px;
  padding-left: 180px;
  margin-top: 64px;
}

footer span {
  font-size: 16px;
  font-family: "Roboto" , sans-serif;
  color: #5c5c5c;
}

.socIcons {
  float: right;
}

.socIcons span {
  margin-right: 33px;
}

.socIcons span:nth-child(3) {
  margin-right: 0px;
}

.socIcons span img {
  height: 30px;
}
